<template>
  <div class="left-content" :key="instanceKey">
    <div class="left-bg-box">
      <div class="tab-box">
        <div
          class="tab-item"
          :class="{ active: curTabIndex === item.tabIndex }"
          v-for="(item, index) in tabList"
          :key="index"
          @click="handleTabClick(item)"
          >{{ item.tabLabel }}</div
        >
      </div>
      <div class="tab-content-box">
        <VideoMonitor
          v-if="curTabIndex === '1'"
          :searchStationInfo="props.searchStationInfo"
          @select="handleSelect"
        />
        <RealStatus
          :tabIndex="curTabIndex"
          v-if="curTabIndex === '2' || curTabIndex === '3' || curTabIndex === '4'"
        />
      </div>
    </div>
  </div>

  <!-- 视频 -->
  <div
    v-if="curTabIndex === '1'"
    class="right-content"
    style="opacity: 1; background-color: #04284c"
  >
    <div class="right-bg-box">
      <div id="gridView" class="video-list-layout">
        <div
          class="monitor-video-item-layout grid-item"
          v-for="(video, index) in mainGridVideos"
          :key="index"
          :id="'videoDiv' + index"
        >
          <div
            class="monitor-video-item"
            style="width: 100%; height: 100%; background-color: black"
          >
            <div
              :id="mainGridVideos[index].id"
              class="video-box"
              width="100%"
              height="100%"
              @click="selectVideo(video, index)"
              @dblclick="dbClickVideo(mainGridVideos[index], index)"
            >
              视频
            </div>
            <div class="title-box">
              <span
                >{{ mainGridVideos[index].stationName }} -
                {{ mainGridVideos[index].cameraName }}</span
              >
              <Icon
                icon="fluent-mdl2:scale-volume"
                style="font-size: 0.14rem"
                @click="fullScreen(video)"
              />
            </div>
          </div>
        </div>
      </div>

      <div id="fullView" class="monitor-video-item hidden">
        <div class="video-box">
          <div
            id="fullVideo"
            style="width: 100%; height: 76vh; min-height: 650px; background-color: black"
          ></div>
          <div class="title-box">
            <span>{{ fullVideo.stationName }} - {{ fullVideo.cameraName }}</span>
            <Icon class="full" :icon="'uil:compress-arrows'" size="20" @click="exitFullScreen()" />
          </div>

          <Icon class="background-image" :icon="'tabler:direction-arrows'" size="80" />
          <div style="position: absolute; right: 20px; bottom: 97px">
            <div style="display: flex; flex-direction: column; align-items: center">
              <a-button class="arrowButton" @click="control(fullVideo, 0)">上</a-button>
            </div>
            <div style="height: 20px">
              <a-button class="arrowButton" @click="control(fullVideo, 2)">左</a-button>
              <a-button class="arrowButton" @click="control(fullVideo, 3)">右</a-button>
            </div>
            <div style="display: flex; flex-direction: column; align-items: center">
              <a-button class="arrowButton" @click="control(fullVideo, 1)">下</a-button>
            </div>
            <div
              style="display: flex; flex-direction: column; align-items: center; margin-top: 20px"
            >
              <Icon
                class="controlButton"
                :icon="'material-symbols:add'"
                size="30"
                style="display: inline-block"
                @click="control(fullVideo, 8)"
            /></div>
            <div style="flex-direction: column; margin-top: 10px; text-align: center"
              ><Icon
                class="controlButton"
                :icon="'ant-design:line-outlined'"
                size="30"
                style="display: inline-block"
                @click="control(fullVideo, 9)"
              />
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>

  <!-- 智能设备弹框 -->
  <RealStatusModal v-if="showModal" :closeModal="closeModal" :infoData="infoData" />
</template>

<script lang="ts" setup>
  import { ref, onMounted, UnwrapRef, reactive } from 'vue';
  import VideoMonitor from './VideoMonitor.vue';
  import RealStatus from './RealStatus.vue';
  import RealStatusModal from './RealStatusModal.vue';
  import { assign } from 'lodash-es';
  import { defHttp } from '/@/utils/http/axios';
  import Icon from '@/components/Icon/Icon.vue';
  import { getPlayer, EzuiktProto, playVideo, destoryPlayer } from '/@/utils/flvtools';
  import { MsgManager } from '/@/message/MsgManager';
  import { buildUUID } from '@/utils/uuid';
  
  interface Props {
    searchStationInfo?: {};
  }

  const instanceKey = ref(buildUUID());
  const props = withDefaults(defineProps<Props>(), {});

  const curTabIndex = ref('1');
  const tabList = ref([
    {
      tabIndex: '1',
      tabLabel: '视频监控',
    },
    {
      tabIndex: '2',
      tabLabel: '电子工牌',
    },
    {
      tabIndex: '3',
      tabLabel: '巡检终端',
    },
    {
      tabIndex: '4',
      tabLabel: '行车记录仪',
    },
  ]);

  // 智能设备（电子工牌、巡检仪、行车记录仪） 开始
  const showModal = ref<boolean>(false);
  const infoData = ref({}) as any;

  const initModalInfo = ({ type, record, removeEvent }) => {
    infoData.value = record;
    infoData.value.bizType = type;
    showModal.value = true;
    removeEvent();
  };

  const closeModal = () => {
    showModal.value = false;
  };

  const listenSmartDeviceModal = () => {
    // 监听智能设备弹窗信息
    MsgManager.getInstance().listen('smart-device-modal-info', initModalInfo);
    // 监听智能设备弹窗打开关闭
    MsgManager.getInstance().listen('smart-device-modal', (value) => {
      showModal.value = value;
    });
  };
  // 智能设备（电子工牌、巡检仪、行车记录仪） 结束

  //video 开始
  const videos = ref([]);
  const selectVideoPanel = reactive<any>({});
  let selectIndex = 0;
  const fullVideo = reactive<any>({});
  const fullVideoView = ref(false);
  const fullFlvPlayer = ref(null);
  const flvPlayerList = ref([]);
  const deviceSearchInfo: UnwrapRef<any> = reactive({});

  function selectVideo(item, index) {
    assign(selectVideoPanel, item);
    selectIndex = index;
  }

  const createVideo = () => {
    for (let i = 0; i < mainGridVideos.value.length; i++) {
      const flvPlayer = ref(null);
      setTimeout(function () {
        if (mainGridVideos.value[i].url != null && mainGridVideos.value[i].url != '') {
          const item = mainGridVideos.value[i];
          const argments = [item.id, item.url, item.accessToken, true, EzuiktProto.HTTP];
          flvPlayer.value = getPlayer(...argments);
          flvPlayerList.value.push(flvPlayer.value);
        }
      }, 300);
      flvPlayerList.value.push(flvPlayer.value);
    }
  };

  function dbClickVideo(item, index) {
    destoryVideo(flvPlayerList.value[index]);

    setTimeout(function () {
      const argments = [item.id, item.url, item.accessToken, true, EzuiktProto.HTTP];
      flvPlayerList.value[index] = getPlayer(...argments);
    }, 10);
  }

  function handleSelect(node) {
    if (node != null) {
      if (node.nodeKindId != 'station') {
        setTimeout(function () {
          if (selectVideoPanel.id != null && selectVideoPanel.id != '') {
            destoryVideo(flvPlayerList.value[selectIndex]);

            if (node.url != null && node.url != '') {
              mainGridVideos.value[selectIndex].cameraName = node.name;
              mainGridVideos.value[selectIndex].stationName = node.powerStationName;
              mainGridVideos.value[selectIndex].url = node.url;
              mainGridVideos.value[selectIndex].accessToken = node.accessToken;
              mainGridVideos.value[selectIndex].deviceSerial = node.deviceSerial;
              mainGridVideos.value[selectIndex].channelNo = node.channelNo;
              mainGridVideos.value[selectIndex].id = node.id + new Date();

              destoryVideo(flvPlayerList.value[selectIndex]);

              setTimeout(function () {
                const argments = [
                  selectVideoPanel.id,
                  node.url,
                  node.accessToken,
                  true,
                  EzuiktProto.HTTP,
                ];
                flvPlayerList.value[selectIndex] = getPlayer(...argments);
              }, 300);

              assign(selectVideoPanel, mainGridVideos.value[selectIndex]);
            }
          }
        }, 300);
      }
    }
  }

  function fullScreen(item) {
    assign(fullVideo, item);
    var div = document.getElementById('fullView');
    div.classList.remove('hidden'); // 显示div
    var div2 = document.getElementById('gridView');
    div2.classList.add('hidden'); // 显示div
    div2.classList.remove('video-list-layout'); // 显示div

    fullVideoView.value = true;
    setTimeout(function () {
      document.querySelectorAll('.arrowButton').forEach(function (item) {
        item.classList.remove('ant-btn');
      });
      if (fullVideo.url != null && fullVideo.url != '') {
        setTimeout(function () {
          fullFlvPlayer.value = getPlayer(
            'fullVideo',
            item.url,
            item.accessToken,
            true,
            EzuiktProto.HTTP,
          );
        }, 300);
      }
    }, 300);
  }

  function exitFullScreen() {
    var div = document.getElementById('fullView');
    div.classList.add('hidden'); // 显示div
    var div2 = document.getElementById('gridView');
    div2.classList.remove('hidden'); // 显示div
    div2.classList.add('video-list-layout'); // 显示div
    destoryVideo(fullFlvPlayer.value);
    fullVideoView.value = false;
  }

  function destoryVideo(flvPlayer) {
    if (flvPlayer != null) {
      flvPlayer.destroy();
      flvPlayer = null;
    }
  }
  function control(item, direction) {
    item.direction = direction;
    videoControl(item);
  }
  const videoControl = (params) =>
    defHttp.get(
      {
        url: '/baseset/videoInfo/videoControl',
        params,
      },
      {},
    );
  const mainGridVideos = ref();

  const getVideoPageDataApi = (params) =>
    defHttp.get(
      {
        url: '/baseset/videoInfo/list',
        params,
      },
      { isOnlyResult: true },
    );
  //video 结束

  const handleTabClick = (item) => {
    curTabIndex.value = item.tabIndex;
    showModal.value = false;
    if (item.tabIndex == 1) {
      setTimeout(function () {
        createVideo();
        document.querySelectorAll('.grid-item').forEach(function (data) {
          data.addEventListener('click', function () {
            // 移除其他项的选中状态
            document.querySelectorAll('.grid-item.selected').forEach(function (selectedItem) {
              selectedItem.classList.remove('selected');
            });
            // 添加当前点击项的选中状态
            this.classList.add('selected');
          });
        });
      }, 300);
    }
  };

  // 播放器统一销毁函数
  const destoryAllPlayer = () => {
    try {
      const list = flvPlayerList.value;
      for (let i = 0; i < list.length; i++) {
        destoryPlayer(list[i]);
        list[i] = null;
      }
      destoryPlayer(fullFlvPlayer.value);
      fullFlvPlayer.value = null;
      flvPlayerList.value = [];
    } catch (error) {
      console.error('destory all player error: ', error);
    }
  };

  // 销毁函数
  const destory = () => {
    destoryAllPlayer();
  };

  // 更新组件函数
  const handleUpdate = () => {
    instanceKey.value = buildUUID();
  };

  // 暴露函数
  defineExpose({
    handleUpdate,
    destory,
  });

  onMounted(async () => {
    listenSmartDeviceModal();
    assign(deviceSearchInfo, props.searchStationInfo);
    videos.value = await getVideoPageDataApi({
      nodeKindId: 'station',
      searchId: deviceSearchInfo.stationUUId,
    });
    mainGridVideos.value = videos.value.slice(0, 9);
    setTimeout(function () {
      createVideo();
      document.querySelectorAll('.grid-item').forEach(function (item) {
        item.addEventListener('click', function () {
          // 移除其他项的选中状态
          document.querySelectorAll('.grid-item.selected').forEach(function (selectedItem) {
            selectedItem.classList.remove('selected');
          });
          // 添加当前点击项的选中状态
          this.classList.add('selected');
        });
      });
    }, 300);
  });
</script>

